#flex-container{
  height:100vw;
  width: 100vw;
  display:flex;
  flex-direction: row;
}

.counter-container-1{
  height: 100%;
  width: 100%;
  background-color: rgb(107, 50, 50);
  text-align: center;
}

.counter-container-2{
  height: 100%;
  width: 100%;
  background-color: rgb(37, 37, 88);
  text-align: center;
}

.counter-container-3{
  height: 100%;
  width: 100%;
  background-color: rgb(70, 70, 33);
  text-align: center;
}

.counter {
  font-size: 5em;
  font-weight: bold;
}

.counter-title {
  font-size: 4em;
  padding-top: 25%;
}

.button-container {
  font-size: 4em;
}

.counter-button {
  display: inline-block;
  width: 5vw;
  height: 5vw;

  background: none;
	color: inherit;
	border: none;
	padding: 0;
	font: inherit;
	cursor: pointer;
	outline: inherit;
}